.videoCard{
    width: 270px;
    margin-bottom: 40px;

}
.videoCard_thumbnail{
    height: 140px;
    width: 250px;
}
.videoCard_info{
    display: flex;
    margin-top: 10px;
    padding-right: 30px;
}
.videoCard_text{
    margin-left: 15px;
}
.videoCard_text > h4{
    font-size: 14px;
    margin-bottom: 5px;
}
.videoCard_text > p{
    font-size: 14px;
    color: gray;
}
.videoCard_avatar{
    height: 30px !important;/*we use important because avatar is a material ui so to overwrite in css we use important*/
    width: 30px !important;
}